.hero {
  box-sizing: border-box;
  z-index: 1;
  background-image: url("bcg.d58dc84a.d58dc84a.png");
  background-position: 50%;
  background-repeat: no-repeat;
  background-size: cover;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
  max-height: 1024px;
  margin: 0 auto;
  padding: 90px 16px 96px;
  display: flex;
  position: relative;
}

.hero:after {
  content: "";
  z-index: 2;
  background: linear-gradient(360deg, #edd789e6 0%, #e98283e6 100%);
  width: 100%;
  height: 100%;
  display: block;
  position: absolute;
  inset: 0;
}

.hero__title {
  z-index: 3;
  color: #fff;
  text-align: center;
  vertical-align: middle;
  box-sizing: border-box;
  margin: 0 auto;
  padding: 0;
  font-family: Kaushan Script, sans-serif;
  font-size: 36px;
  font-weight: 400;
  position: relative;
}

.hero__subtitle {
  z-index: 3;
  color: #fff;
  text-transform: uppercase;
  text-align: center;
  vertical-align: middle;
  box-sizing: border-box;
  width: 288px;
  margin: 34px auto;
  padding: 0;
  font-family: Montserrat, sans-serif;
  font-size: 48px;
  font-weight: 700;
  line-height: 100%;
  position: relative;
}

.hero__separator {
  z-index: 3;
  background-color: #fff;
  border: none;
  width: 134px;
  height: 4px;
  margin: 0 auto 50px;
  padding: 0;
  display: flex;
  position: relative;
}

.hero__button {
  z-index: 3;
  color: #fff;
  text-align: center;
  vertical-align: middle;
  text-transform: uppercase;
  cursor: pointer;
  background-color: #0000;
  border: 4px solid #fff;
  justify-content: center;
  align-items: center;
  width: 284px;
  height: 54px;
  margin: 0 auto;
  padding: 0;
  font-family: Raleway, sans-serif;
  font-size: 18px;
  font-weight: 700;
  line-height: 100%;
  display: flex;
  position: relative;
}

.hero__button:hover {
  color: #e4be8d;
  background-color: #fff;
}

.hero__button:active {
  color: #e4be8d;
  background-color: #fff;
  border: 4px solid #e4be8d;
}

@media (width >= 768px) {
  .hero {
    background-image: url("bcg_2.6e8801f5.6e8801f5.png");
    padding: 120px 0 136px;
  }

  .hero__title {
    font-size: 48px;
    font-weight: 400;
    line-height: 100%;
  }

  .hero__subtitle {
    flex-flow: column wrap;
    width: fit-content;
    height: 250px;
    margin: 0 auto;
    font-size: 96px;
    display: flex;
  }

  .hero__separator {
    width: 114px;
    margin: 30px auto 48px;
  }

  .hero__button {
    width: 318px;
    height: 54px;
  }
}

@media (width >= 1300px) {
  .hero {
    background-image: url("bcg_2.6e8801f5.6e8801f5.png");
    padding: 178px 0 200px;
  }

  .hero__title {
    font-size: 64px;
    font-weight: 400;
    line-height: 80px;
  }

  .hero__subtitle {
    flex-flow: column wrap;
    width: fit-content;
    height: 370px;
    font-size: 144px;
    display: flex;
  }

  .hero__separator {
    width: 170px;
    margin: 44px auto 72px;
  }

  .hero__button {
    width: 370px;
    height: 80px;
    font-size: 24px;
  }
}
